.footer {
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 0 20px;
    font-size: 14px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    height: var(--footer-height);
  }
  .footer ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
  }
  
  .copyright {
    text-align: left;
    -ms-flex: 1 1;
        flex: 1 1;
  }
  
  .contact-links {
    text-align: right;
    -ms-flex: 1 1;
        flex: 1 1;
  }
  
  .FooterLink {
    padding: 0 14px;
    display: inline-block;
  }
  .FooterLink:last-child {
    border-right: none;
  }
  .FooterLink a {
    text-decoration: none;
    color: var(--color-blue);
  }
  .FooterLink a:hover, .FooterLink a:focus {
    text-decoration: underline;
  }
  


