@import './General/Grid.css';
@import './General/Variables.css';
@import url('https://fonts.googleapis.com/css?family=Roboto+Condensed:700|Source+Sans+Pro:300, 700');
body {
    background-color: var(--color-off-white);
    color: var(--color-dark-gray);
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Roboto Condensed', sans-serif;
    font-weight: 700;
    color: var(--color-dark-gray);
}

h1.primary,
h2.primary,
h3.primary,
h4.primary,
h5.primary,
h6.primary {
    color: var(--color-blue);
    text-transform: uppercase;
}

h1.secondary,
h2.secondary,
h3.secondary,
h4.secondary,
h5.secondary,
h6.secondary {
    color: var(--color-orange);
    text-transform: uppercase;
}

h1.tertiary,
h2.tertiary,
h3.tertiary,
h4.tertiary,
h5.tertiary,
h6.tertiary {
    color: var(--color-medium-gray);
    text-transform: uppercase;
}

.body {
    padding-bottom: var(--footer-height);
}

.App {
    font-family: 'Source Sans Pro', sans-serif;
    font-weight: 300;
    max-width: 1366px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    margin: 0 auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    min-height: 100%;
    position: relative;
}

@media (max-width: 767px) {
    .App {
        padding-left: 1em;
        padding-right: 1em;
    }
}

@media (min-width: 768px) {
    .App {
        padding-left: 2em;
        padding-right: 2em;
    }
}


